@import 'utils/reset';
@import 'utils/vars';
@import 'utils/buttons.scss';

html {
  line-height : $mainLineHeight;
  font-size   : $mainFontSize;
  color       : $mainFontColor;
  height      : 100%;
  font-family : $mainFont;
}

body {
  margin                  : 0;
  padding                 : 0;
  font-family             : $mainFont;
  font-size               : $mainFontSize;
  font-weight             : 400;
  -webkit-font-smoothing  : antialiased;
  -moz-osx-font-smoothing : grayscale;
  background-color        : #fff;
  color                   : $mainFontColor;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin : 0;
}

a {
  text-decoration : none;
}

button {
  cursor : pointer;
}

ul,
li {
  margin          : 0;
  padding         : 0;
  list-style-type : none;
}

.container {
  width     : 100%;
  flex      : 0 0 auto;
  position  : relative;
  margin    : 0 auto;
  max-width : $desktopWidth;
}

///////////////////////////////////////////////////////////
.header {
  // .header__title
  &__title {
    margin-top : 60px;

  }

  // .header__nav
  &__nav {
  }

  // .header__menu
  &__menu {
    margin-top    : 46px;
    margin-bottom : 47px;
  }

  // .header__promo
  &__promo {
  }
}

.container {
}

.main-title {
  font-family    : $addFont;
  font-weight    : 900;
  font-size      : 35px;
  line-height    : 47px;
  letter-spacing : 1px;
  text-align     : center;

}

.main-menu {
  display         : flex;
  justify-content : space-between;
  // .main-menu--light
  &--light {
  }

  // .main-menu__item
  &__item {
  }

  // .main-menu__link
  &__link {
    font-size      : 15px;
    line-height    : 18px;
    letter-spacing : 2px;
    text-transform : uppercase;




    // .main-menu__link--dark
    &--dark {
      color : #000;
    }

    // .main-menu__link--light
    &--light {
      color : #fff;
    }
    &:hover,
    &:active,
    &:focus {
      color : $accentFontColor;
    }
  }
}

.promo {
  text-align       : center;
  background-image : url("../img/bg-promo.jpg");
  background-size  : cover;
  padding          : 46px 0 48px;

  // .promo__icon
  &__icon {
    display : inline-block;
  }

  // .promo__title
  &__title {
    font-size      : 72px;
    line-height    : 84px;
    letter-spacing : 2px;
    text-transform : uppercase;
    color          : #fff;
    margin-top     : 32px;

  }

  // .promo__text
  &__text {
    font-size   : 14px;
    line-height : 21px;
    text-align  : center;
    color       : #fff;
    margin      : 139px auto 0;
    max-width   : 456px;

  }
}

.content {
}

.reviews {
  padding : 44px 0 51px;

  // .reviews__title
  &__title {
    font-weight    : bold;
    font-size      : 17px;
    line-height    : 20px;
    letter-spacing : 2px;
    text-align     : center;

  }

  // .reviews__block
  &__block {
    margin-top : 20px;
  }
}

.reviews-block {
  display         : flex;
  justify-content : space-between;
  margin-left     : 15px;
  margin-right    : 15px;

  // .reviews-block__item
  &__item {
    margin : 0 15px;
    flex   : 1 1 calc(100% / 3 - 30px);
  }
}

.review {
  // .review__header
  &__header {
  }

  // .review__image
  &__image {
  }

  // .review__title
  &__title {
    font-family : $addFont;
    font-weight : 900;
    font-size   : 23px;
    line-height : 31px;
    margin-top  : 20px;
    color       : $mainFontColor;
    display     : inline-block;

    &:hover,
    &:active,
    &:focus {
      color : $accentFontColor;
    }
  }

  // .review__text
  &__text {
    margin-top : 11px;
  }
}

.actions {
  text-align       : center;
  background-color : #f7f7f7;
  mix-blend-mode   : normal;
  overflow         : hidden;

  // .actions__inner
  &__inner {
    background-color : #fff;
    border-radius    : 50%;
    padding          : 100px 170px;
    margin           : -70px auto;
    width            : 707px;
    height           : 707px;

  }

  // .actions__title
  &__title {
    font-weight    : bold;
    font-size      : 17px;
    line-height    : 20px;
    letter-spacing : 2px;
  }

  // .actions__block
  &__block {
  }
}

.actions-block {
  // .actions-block__inner
  &__inner {
  }

  // .actions-block__item
  &__item {
    margin-top : 23px

  }

  // .actions-block__footer
  &__footer {
  }

  // .actions-block__more.btn.btn
  &__more {
    margin-top : 25px;
  }
}

.action {
  padding-bottom : 20px;
  position       : relative;
  padding-bottom : 20px;

  &::after {
    content          : '';
    position         : absolute;
    width            : 100px;
    height           : 1px;
    opacity          : 0.4;
    background-color : rgba(0, 0, 0, 0.2);
    left             : 50%;
    margin-left      : -50px;
    bottom           : 0;
  }

  &:last-child::after {
    content : none;

  }

  // .action__header
  &__header {
  }

  // .action__title
  &__title {

    font-family : $addFont;
    font-weight : 900;
    font-size   : 23px;
    line-height : 31px;

  }

  // .action__text
  &__text {
  }
}

.btn {
  font-weight    : bold;
  font-size      : 12px;
  line-height    : 14px;
  letter-spacing : 2px;
  text-transform : uppercase;
  padding        : 17px 30px 15px;
  display        : inline-block;
  transition     : color, background-color 0.3s;
  position       : relative;


  // .btn--fill
  &--fill {
    color            : #fff;
    background-color : $accentFontColor;
    border           : 1px solid $accentFontColor;


    &:hover,
    &:active,
    &:focus {
      color            : $accentFontColor;
      background-color : transparent;

      // .btn__icon
      .btn__icon {
        fill : $accentFontColor;
      }
    }
  }

  // .btn--right-arrow
  &--right-arrow {
    padding-right : 60px;
  }

  // .btn__icon
  &__icon {
    fill          : #fff;

    position      : absolute;
    right         : 29px;
    top           : 50%;
    transform     : translateY(-50%);
  }
}


.footer {
  background-color : #575757;
  padding          : 26px 0 46px;
  margin-top       : 50px;

  // .footer__title
  &__title {
    font-family    : $addFont;
    font-style     : normal;
    font-weight    : 900;
    font-size      : 28px;
    line-height    : 37px;
    letter-spacing : 1px;
    color          : #fff;
    text-align     : center;
  }

  // .footer__nav
  &__nav {
    margin-top : 32px;
  }

  // .footer__menu
  &__menu {
  }

  // .footer__line
  &__line {
    opacity          : .4;
    height           : 1px;
    padding          : 0;
    margin           : 46px 0 0;
    background-color : rgb(255, 255, 255);
    border           : rgb(255, 222, 173);

  }

  // .footer__socials
  &__socials {
  }
}

.socials {
  display         : flex;
  justify-content : center;
  margin-top      : 42px;

  // .socials__item
  &__item {
    margin : 0 7px
  }
}

.social {
  width               : 47px;
  height              : 47px;
  background-position : center;
  background-repeat   : no-repeat;
  background-color    : #fff;


  // .social--in
  &--in {
    background-image : url("../img/social/1.svg");
  }

  // .social--fb
  &--fb {
    background-image : url("../img/social/2.svg");
  }

  // .social--tw
  &--tw {
    background-image : url("../img/social/3.svg");
  }
}





